@mixin lineAndSpacing($height:100%, $space: -0.02em){
    line-height: $height;
    letter-spacing: $space;
}
@mixin positionAbsolute(){
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
@mixin positionAbsoluteImage(){
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: 0;
}