.community__section_content{
  padding: 0 calc(64 / 1440 * 100%) 80px;

}
.community-content{
  margin-top: 156px;
  display: flex;
  justify-content: flex-start;
  align-items: center;  
  margin-bottom: 80px;
}
.community-content__title{
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: calc(584 / 1312 * 100%);
}
.community-content__description{
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: calc(336 / 1312 * 100%);
}


.community__tabs{
  margin-top: 13px;
}
.community__section_poster{
  position: relative;
  margin-top: -485px;
  z-index: 1;
}
.community__section_media{
  padding-bottom: 156px;
}

.community_section_vacancy{
  padding-top: 156px;
  padding-bottom: 156px;
}
.community__piano{
  margin-top: 104px;
}
.community__mailing{
  padding-top: calc(577 / 1440 * 100%);
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
  &-inner{
    position: absolute;
    top: calc(184 / 577 * 100%);
    left: calc(64 / 1440 * 100%);
    max-width: 365px;
  }
  &-img{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: -1;
  }
  &-form{
    position: absolute;
    display: block;
    top: calc(268 / 577 * 100%);
    right: calc(64 / 1440 * 100%);
    .input_mailing{
      background-color:transparent;
      border: none;
      border-bottom: 1px solid rgba(255,255,255,0.2);
      font-size: 56px;
      color: white;
      line-height: 100%;
      padding-bottom: 8px;
      outline: none;
      &:focus{
        border-bottom: 1px solid rgba(255,255,255,1);
      }
      &::placeholder{
        opacity: 0.3;
      }
    }
    button[type="submit"]{
      outline: none;
      cursor: pointer;
      background-color: transparent;
      border:none;
      position: absolute;
      bottom: -48px;
      right: 0;
      width: 168px;
      text-align: left;
      background-image: url("../images/arrow_right_white.svg");
      background-repeat: no-repeat;
      background-position: center right;
    }
  }
}