.header{
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  padding-top: 32px;
  &_dark{
    .menu__burger{
      &:after, &:before{
        background-color:$font-color-dark;
      }
    }
    .menu__title{
      color: $font-color-dark;
    }
    .lang{
      color: $font-color-dark;
    }
    .search{
      color: $font-color-dark;
      background: left top 2px no-repeat url('../images/search_icon_dark.svg');
      &::placeholder{
        color: $font-color-dark;
      }
      &__input{
        color: $font-color-dark;
        &::placeholder{
          color: $font-color-dark;
        }
      }
    }
    .phone{
      color: $font-color-dark;
      background: right center no-repeat url('../images/whatsapp_icon_dark.svg');
    }
    .request{
      color: $font-color-dark;
      border: 1px solid $font-color-dark;
    }
  }
}

.header__inner{
  display: flex;
  position: relative;
  z-index: 10;
  flex-direction:row;
  justify-content: flex-start;
  align-items: center;
}

.menu__btn{
  cursor: pointer;
  transition: 0.2s;
  &_active{
    .menu__burger{
      &:after{
        top: 50%;
        transform: rotate(45deg);
      }
      &:before{
        transform: rotate(-45deg);
        top: 50%
      }
    }
    
  }
}
.menu__burger{
  display: inline-block;
  height: 7px;
  width: 24px;
  position: relative;
  margin-right: 16px;
  &:after, &:before{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: #fff;
    position: absolute;
    transition: 0.2s;
  }
  &:after{
    left: 0;
    bottom: 0;
  }
  &:before{
    left: 0;
    top: 0;
  }
}
.menu__title{
  font-family: $font-light;
  font-size: 12px;
  text-transform: uppercase;
  color:white;
  display: inline-block;
}
.menu__title_dark{
  color: $font-color-dark;
}

.header__logo{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.lang{
  font-family: $font-light;
  font-size: 12px;
  text-transform: uppercase;
  color: white;
  margin-left: 91px;
}

.header__phone{  
  margin-left: auto;
}
.phone{
  display: block;
  line-height: 24px;
  height: 24px;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  padding-right: 32px;
  background: right center no-repeat url('../images/whatsapp_icon.svg');
}
.header__request{
  margin-left: 88px;
}
.request{
  display: block;
  border: 1px solid white;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  width: 136px;
  height: 32px;
  line-height: 32px;
  text-align: center;
}

.header__search{
  margin-left: 97px;
  position: relative;
  max-width: 360px;
  width: 100%;
  &_active{
    z-index: 100;
    &:before{
      content:'';
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      background-color: #333;
    }
    .search{
      border-bottom: 1px solid rgba(255,255,255,.1);
    }
    .search__button_close{      
    display: inline-block;
    }
  }
}
.search{
  display: flex;
  justify-content: space-between;
  padding-left: 22px;
  padding-bottom: 9px;
  background: left top 2px no-repeat url('../images/search_icon.svg');
  position: relative;
  &__results{
    position: absolute;
    left: 0;
    top: calc(100% + 40px);
    width: 100%;
    display: none;
    &_active{
      display: block;
    }
  }
  &__result{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    max-width: 360px;
    margin-bottom: 16px;

    &-image{
      flex-shrink: 0;
      flex-grow: 0;
      border-radius: 50%;
      overflow: hidden;
      width: 56px;
      height: 56px;
    }
    &-info{
      margin-left: 16px;
      flex-grow: 1;
    }
    &-label{
      font-size: 12px;
      font-family: $font-light;
      color: rgba(255,255,255,.6);
      text-transform: uppercase;
      margin-bottom: 4px;
    }
    &-title{
      font-size: 14px;
      font-family: $font-regular;
      color: white;
    }
    &_not-found{
      color: rgba(255,255,255,.6);
      font-family: $font-light;
      font-size: 14px;
      font-weight: 300;
    }
    &_all{
      display: block;
      padding-right: 82px;
      background-image: url('../images/arrow_right_white.svg');
      background-position: right center;
      background-repeat: no-repeat;
      margin-top: 48px;
      max-width: 177px;
      font-size: 12px;
      font-family: $font-light;
      color: white;
      @include lineAndSpacing(100%);
      text-transform: uppercase;
    }
  }
  &__button_close{
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    background-color: transparent;
    background-image: url('../images/search_close.svg');
    background-position: center;
    background-repeat: no-repeat;
    width: 19px;
    height: 19px;
    padding: 5px;
    border: none;
    cursor: pointer;
  }
}
.search__input{
  background-color:transparent;
  border: none;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  width: 100%;
  &::placeholder{
    font-size: 12px;
    font-family: $font-light;
    text-transform: uppercase;
    opacity: 0.6;
    color: white;
  }
  &:focus{
    border: none;
    outline: none;
  }
}

.header-menu{
  transition: 0.2s;
  &_active{
    left: 0;
  }
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: -150%;
  padding-top: calc(209 / 1440 * 100%);
  padding-left: 68px;
  padding-right: 68px;
  padding-bottom: 40px;
  background-color: rgba(51,51,51,.9);
  &__label{
    display: block;
    margin:0 auto;
  }
  &__navigation{
    display: block;
    margin: 0 auto;
    max-width: 660px;    
    margin-bottom: 104px;
  }
  &__list{
    display: flex;
    flex-flow: row wrap;    
    list-style:none; 
    align-items: center;
    justify-content: center;
    margin-bottom: -32px;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-bottom: 32px;
  }
  &__link{
    color: white;
    font-family: $font-medium;
    font-weight: 500;
    font-size: 23px;
    @include lineAndSpacing(90%);
  }
  &__separator{
    display: inline-block;
    pointer-events: none;
    width: 56px;
    height: 1px;
    background-color: rgba(255,255,255,0.1);
    margin: 0 16px;
  }
  &__projects{ 
    margin: 0 auto;
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    max-width: calc(979 / 1440 * 100%);
   
  }
  &__project{
    flex-shrink: 0;
    flex-grow: 0;
    flex-basis: calc(313 / 979 * 100%);
    position: relative;
    &:after{
      content: '';
      display: block;
      width: 100%;
      padding-top: calc(176 / 313 * 100%);
    }
  }
  &__label{
    display: block;
    max-width: 200px;
    text-align: center;
    margin: 0 auto;
  }
  &__socials{
    position: absolute;
    left: calc(64 / 1440 * 100%);
    bottom: calc(40 / 744 * 100%);
    width: calc(1312 / 1440 * 100%);
  }
}

