}
.photocard{
  width: calc(416 / 1312 * 100%);   
  position: relative;
  &:after{
   content: '';
   display: block;
   width: 100%;
   padding-top: 133.4%;
 }
}

.photocard_square{
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: calc(640 / 1312 * 100%);
  &:after{
    padding-top: 100%;
  }
}


.piano__list{
  position: relative;
  z-index: 2;
}
.piano{
  position: relative;
  &__item{
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    color: white;
    padding: 40px 0;
    border-top: 1px solid rgba(255,255,255,.2);
    transition: 0.2s;
    cursor: pointer;
    &:last-child{
      border-bottom: 1px solid rgba(255,255,255,.2);
      
    }
    &:hover{
      .piano__title, .piano__label{
        opacity:1;
      }
      .piano__desc{
        opacity: 0.6;
      }
    }
  }
  &__label{
    align-self: flex-start;
    opacity: 0.6;
    transition: 0.2s;   
    flex: 0 0 85px;
  }
  &__title{
    font-family: $font-medium;
    font-size: 56px;
    opacity: 0.2;
    //opacity: 0.6;
    transition: 0.2s;
    line-height: 50px;
  }
  &__desc{
    font-family: $font-light;
    font-size: 12px;
    opacity: 0.6;
    max-width: 304px;
    margin-left: auto;
    @include lineAndSpacing(160%);
    opacity: 0;
    transition: 0.2s;
  }
  
  &__images{
    position: absolute;
    width: 350px;
    height: 254px;
    transform: translateY(-50%) rotate(6deg);
    overflow: hidden;
    top: 50%;
    left: 43.125%;
    &_pos_top{
      top: 77px;
      transform: rotate(6deg);
    }
  }
  &__image{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    opacity: 0;
    transition: 0.2s;  
    object-fit: cover;
    width: 100%;
  }
  &__image_visible{
    opacity: 1;
  }
  &_dark{
    .piano__item{
      color: #1B1B1B;      
      border-top: 1px solid rgba(27, 27, 27, 0.2);     
      &:last-child{
        border-bottom: 1px solid rgba(27, 27, 27, 0.2);        
      }      
    }
  }
}

.articles-preview{
  display: flex;
  justify-content: space-between;
  
  &__column{
    flex: 0 0 calc(336 / 1312 * 100%);
    &_wide{
      flex: 0 0 calc(528 / 1312 * 100%);
    }
  }
}
.article-preview{
  cursor: pointer;
  display: block;
  color: $font-color-dark;
  border-bottom: 1px solid rgba(0,0,0,.3);
  position: relative;
  &:after{
    content: '';
    display: block;
    width: 100%;

  }
  &__inner{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &__image{
    margin-bottom: 32px;    
    position: relative;
    &:after{
      content: '';
      display: block;
      width: 100%;   
      overflow: hidden;
    }      
    img{
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      z-index: 5;
    }
  }
  &__label{
    margin-bottom: 16px;
  }
  &__title{
    font-size: 24px;
    font-family: $font-regular;
    font-weight: 400;
    @include lineAndSpacing(120%);
    margin-bottom: 16px;
  }
  &__text{
    font-size: 14px;
    font-family: $font-light;
    opacity: 0.6;
    color: $font-color-dark;
    @include lineAndSpacing(160%);
  }
  &_mb{
    margin-bottom: 40px;
  }
}

