.breadcrumbs{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  &__link{
    display: inline-block;
    font-size: 12px;
    font-family: $font-light;
    color: $font-color-dark;
    opacity: 0.6;
    line-height: 16px;
    @include lineAndSpacing(160%);
  }
  &__separator{
    display: inline-block;
    width: 16px;
    height: 16px;
    background: center center no-repeat url('../images/chevron-right_dark.svg');
  }
  &__separator_light{
    background: center center no-repeat url('../images/chevron-right_white.svg');
  }
  &_light{
    *{
      color: white;
    }
  }
}

.content-page{
  &__first-screen{
   // background-color: white;
  }
  &__info{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    &_light *{
      color: white;
    }
  }
  &__label{
    margin-bottom: 16px;
  }
  &__title{
    font-family: $font-medium;
    font-weight: 500;
    font-size: 56px;
    margin-bottom: 32px;
    max-width: 752px;
    text-align: center; 
    @include lineAndSpacing(100%);
    &_dark{
      color: $font-color-dark;
    }
  }
  &__description{
    text-align: center;    
    max-width: 428px;
  }
}

.pagination{
  &__link{
    font-size: 12px;
    font-family: $font-light;
    color: $font-color-dark;
    text-transform: uppercase;
    padding-left: 8px;
    padding-right: 8px;
    margin-right: 8px;
    opacity: 0.6;
    &_last{
      margin-right: 0;
    }
    &_active{
      opacity: 1;
    }
    &_prev, &_next{
      width: 16px;
      height: 12px;
      opacity: 1;
      &.disactive{
        opacity: 0.6;
        pointer-events: none;
      }
    }
    &_prev{
      background: center center no-repeat url('../images/chevron-left_dark_pagin.svg');
      margin-right: 22px;
    }
    &_next{
      background: center center no-repeat url('../images/chevron-right_dark_pagin.svg');
      margin-left: 22px;
    }
  }
  &_light {
    *{
      color: white;
    }
    .pagination__link_prev{
      background: center center no-repeat url('../images/chevron-left_white_pagin.svg');
    }
    .pagination__link_next{
      background: center center no-repeat url('../images/chevron-right_white_pagin.svg');
    }
  }
}

.grid{  
  width: 100%;
  position:relative;
  z-index: 5;
  &__items{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    &_columns_2{
      flex-wrap: wrap;
      margin-bottom: calc(-64 / 1440 * 100%); 
      .grid__item{
        flex-shrink: 0;
        flex-grow: 0;
        flex-basis: calc(624 / 1312 * 100%);
        margin-bottom: calc(64 / 1440 * 100%);      
        &:after{
          padding-top: calc(660 / 624 * 100%);
        }
      }
    }
  }
  
  &__item{    
    position: relative;  
    &:after{
      content: '';
      display: block;
      width: 100%;
    } 
  }
}