.tabs{
  display: flex;
  justify-content: space-between;  
  border-bottom: 1px solid rgba(0,0,0,.1);
  &_light{
    border-bottom: 1px solid rgba(255,255,255,.1);
    .tab{
      &_active{
        border-bottom: 1px solid rgba(255,255,255,1);
      }
    }
  }
}
.tab{
  outline:none;
  position: relative;
  top: 1px;
  //flex-basis: calc(100% / 4);
  flex-grow: 1;
  display: block;
  background: transparent;
  border: none;
  padding: 16px 0;
  cursor: pointer;
  &_active{
    border-bottom: 1px solid rgba(0,0,0,1);
  }
}

