.gallery{
  &__link{
    opacity: 0;
    pointer-events: none;
    mix-blend-mode: difference;        
    display: block;   
  }  
  &__item:hover &__link{
    opacity: 1;
    pointer-events: all;
  }
  &__nav{
    margin-top: 53px; 
    display: flex;
    justify-content: flex-start;
  }
  &__text{
    font-family: $font-light;
    font-size: 12px;
    @include lineAndSpacing(160%);
    text-transform: uppercase;
    color: #1b1b1b;
    opacity: 0.6;
  }
  &__image{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    object-position: center;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__btn{
    display: block;
    width: 18px;
    height: 13px;
    position: relative;
    opacity: 0.5;
    transition: 0.2s;
    cursor: pointer;
    &_columns{
      margin-left: 24px;
      span{
        display: block;
        width: 100%;
        height: 1px;
        background-color: #000;
        position: absolute;
        &:nth-child(1){
          top: 0;
          left: 0;
        }
        &:nth-child(2){
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
        &:nth-child(3){
          bottom: 0;
          left: 0;
        }
      }
    }  
    &_grid{
      margin-left: auto;
      span{
        display: block;
        height: 1px;
        background-color: #000;
        position: absolute;
        &:nth-child(1){
          top: 0;
          left: 0;
          width: 100%;
        }
        &:nth-child(2){
          top: 50%;
          right: -5px;
          transform: translateY(-50%);
          width: 10px;
        }
        &:nth-child(3){
          bottom: 0;
          right: 0;
          width: 12px;
        }
      }
    }   
    &_active{
      opacity: 1;
    }
  }
  .gallery__btn_more{
    display: block;
    width: 212px;
    height: 212px;
    background-color: transparent;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: $font-light;
    font-size: 12px;
    @include lineAndSpacing(110%);
    text-transform: uppercase;
    margin: 0 auto;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;  
    z-index: 5;  
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border: 1px solid rgba(255,255,255,.2);
      border-right: 1px solid rgba(255,255,255,1);
      border-radius: 50%;
      left: 0;
      top: 0;
      transform: rotate(-45deg);
    }
  }
  &__wrapper_bg_dark{      
    background-color: #333333;
  }
  &__wrapper_grid{
    
    width: 100%;
    padding-top: 232.57%;
    position: relative;
    margin-top: calc(402 / 1440 * 100%);
    .gallery__items{      
      position: absolute;      
      top: 0;
      left: 0;  
      width: 100%;
      height: 100%;  
      z-index: 5;      
    }  
    .gallery__item{
      position: absolute;          
      z-index: 1;
      
      &:nth-child(1){
        width: calc(704 / 1440 * 100%);
        left: 0;
        top: calc(-370 / 3349 * 100%);
        &:after{
          content: '';
          display: block;
          width: 100%;
          padding-top: calc(478 / 704 * 100%);            
        }   
        &:before{
          content: '';
          width: 150px;
          height: 250px;
          position: absolute;
          left: 100%;
          top: calc(60 / 478 * 100%);
          background: white;
        }
        .gallery__link{
          position: absolute;
          top: calc(64 / 478 * 100%);
          left: 100%;
          transform: translateX(-50%);
        }  
      }
      &:nth-child(2){
        width: calc(536 / 1440 * 100%);
        top: calc(-51 / 3349 * 100%);
        right: 0;
        &:after{
          content: '';
          display: block;
          width: 100%;
          padding-top: calc(794 / 536 * 100%);
        }
        .gallery__link{            
          position: absolute;
          top: calc(64 / 478 * 100%);
          left: 100%;
          transform: translateX(-50%);
        } 
      }
      &:nth-child(3){
        width: calc(864 / 1440 * 100%);
        top: calc(963 / 3349 * 100%);
        left: calc(176 / 1440 * 100%);
        &:after{
          content: '';
          display: block;
          width: 100%;
          padding-top: calc(582 / 864 * 100%);
        }
        .gallery__link{
          position: absolute;
          top: calc(64 / 478 * 100%);
          left: 100%;
          transform: translateX(-50%);
        } 
      }
      &:nth-child(4){
        width: calc(704 / 1440 * 100%);
        top: calc(1765 / 3349 * 100%);
        right: 0;
        &:after{
          content: '';
          display: block;
          width: 100%;
          padding-top: calc(478 / 704 * 100%);
        }
        .gallery__link{
          position: absolute;
          top: calc(64 / 478 * 100%);
          left: 100%;
          transform: translateX(-50%);
        }          
      }
      &:nth-child(5){
        width: calc(536 / 1440 * 100%);
        top: calc(2083 / 3349 * 100%);
        left: 0;
        &:after{
          content: '';
          display: block;
          width: 100%;
          padding-top: calc(794 / 536 * 100%);
        }
        .gallery__link{
          position: absolute;
          top: calc(64 / 478 * 100%);
          left: 100%;
          transform: translateX(-50%);
        } 
      }
      &:nth-child(6){
        display: none;
      }        
    }
    .gallery__btn_more{
      top: calc(2980 / 3349 * 100%);
    }
  }
  &__wrapper_columns{
    &_bg_dark{
      background-color: #333333;
    }
    width: 100%;
    padding-top: calc(1606 / 1440 * 100%);
    position: relative;
    
    margin-top: calc(402 / 1440 * 100%);
    .gallery__link{      
      mix-blend-mode: normal;
      position: absolute;
      top: calc(117 / 480 * 100%);
      left: calc( 382 / 640 * 100%);
    }
    .gallery__items{
      position: absolute;      
      top: calc(-375 / 1440 * 100%); 
      width: calc(1312 / 1440 * 100%);
      z-index: 5;
      top: calc( -375 / 1606 * 100%);
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;      
    }   
    .gallery__item{    
      position: relative; 
      width: calc(640 / 1312 * 100%);
      margin-bottom: calc(32 / 1440 * 100%);
      &:after{
        content: '';
        display: block;
        width: 100%;
        padding-top: 75%;
      }   
      &:nth-child(odd)   {
        margin-right: 32px;
      }
    } 
    .gallery__btn_more{     
      top: calc(1238/ 1606 * 100%);
    }
  }
}
