.blockquote{
  width: 100%;  
  max-width: 528px;
  margin: 0 auto;
  position: relative;
  &:before{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -150%) rotate(180deg);
    content: '"';
    display: block;
    font-size: 32px;
    font-family: $font-light;
    line-height: 110%;
  }
}
.blockquote__text{
  font-family: $font-light;
  font-size: 32px;
  text-align: center;
  
  @include lineAndSpacing(110%);
}
.blockquote__text_dark{
  color: #000;
}
.blockquote__author{
  margin-top: 32px;
  text-align: center;
}

.blockquote_type_2{  
  margin: 0;
  *, *:after, *:before, &:after, &:before{
    color: white;
  }
  &:before{
    left: 0;
  }
  .blockquote__text{
    text-align: left;
  }
  .blockquote__author{
    text-align: left;
  }
  .blockquote__description{
    margin-top: 64px;
    margin-left: 112px;
  }

}