.next-page{
    padding-top: calc(577 / 1440 * 100%);
    padding-left: 4.44%;
    position: relative;
    overflow: hidden;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    &:hover{
      .next-page__link{
        opacity: 1;
      }
    }

  &__inner{
    position: absolute;
    top: calc(246 / 577 * 100%);
    left: calc(64 / 1440 * 100%);
  }
  &__inner_size_small{
    max-width: 365px;
  }
  &__img{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: -1;
  }
  &__link{
    display: block;
    position: absolute;
    top: calc(142 / 577 * 100%);
    left: calc(491 / 1440 * 100%);
    opacity: 0;
    transition: 0.2s;
  }
}