.case__logo{
  display: block;
  width: 81px;
  height: 20px;
  opacity: 0.5;
}

.case-info{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.case-info__column{
  margin-right: 48px;
}
.case-info{
  &__type{
    margin-bottom: 8px;
    opacity: 0.4;
  }
  &__data{
    font-size: 12px;
    font-family: $font-light;
  }
}

.slider_page_case{
  width: calc(1312 / 1440 * 100%);
  padding-top: calc(712 / 1440 * 100%);
  margin: 0 auto;
  position: relative;
}
.swiper-container_page_case{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.swiper-slide
{
  &__inner_page_case{
    position: relative;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 66.15%, rgba(0, 0, 0, 0.6) 100%);    
  }
  &__image_page_case{
    display: block;
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: -1;
  }
  &__info_page_case{ 
    top: auto;   
    bottom: calc(24 / 712 * 100%);
    left: calc(32 / 1312 * 100%);
    opacity: 0.6;
  } 
}
.case_bg_dark{
  background-color: $bg-dark;
  padding-top: calc(512px - 156px);
  margin-top: calc(156px - 512px);
}

.section_project-gallery{  
  padding-bottom: calc(80 / 1440 * 100%);
}
.section__wrapper_project-gallery{
  margin-bottom: calc(32 / 1440 * 100%);
}

.images-grid{
  display: flex;
  //width: calc(1312  / 1440 * 100%);
  width: 100%;
  margin: 0 auto;
  &_columns_2{
    justify-content: space-between;
  }
  &__item{
    position:relative;
    overflow: hidden;
    width: 100%;
    &:after{
      content: '';
      display: block;
      padding-top: calc(712 / 1312 * 100%);
      width: 100%;
    }
    &_columns_2{      
      width: calc(640 / 1312 * 100%);   
      &:after{
        content: '';
        display: block;
        padding-top: calc(826 / 640 * 100%);
        width: 100%;
      }
    }
    &:hover .images-grid__inner{
      opacity: 1;
    }
  }
  &__image{
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
  }
  &__inner{
    width: 100%;
    height: 100%;
    background:linear-gradient(180deg, rgba(0, 0, 0, 0) 66.15%, rgba(0, 0, 0, 0.6) 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
    position: absolute;
    top: 0;
    left: 0;
    padding: calc(24 / 1312 * 100%) calc(32 / 1312 * 100%);
    display: flex;
    align-items: flex-end;
    transition: opacity 0.2s;
    opacity: 0;

  }
  &__button{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &__info{
    width: 100%;
    display: flex;
    justify-content: space-between;
    opacity: 0.6;
  }
  &__description, &__time{
    color: white;
  }
}

.section__wrapper_project-info{
  margin-top: calc(42 / 1312 * 100%);
}
.project-info{
  &__description{
    margin-bottom: 16px ;
    opacity: 0.4;
  }
  &__members{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  &__member{
    margin-right: 24px;
    color: white;
  }
  &__count{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 1px solid white;
    opacity: 0.4;
    margin-right: 8px;
  }
  &__photographer{
    color: white;
    margin-left: auto;
    span:first-child{
      opacity: 0.4;
      margin-right: 8px;
    }
  }
}

.swiper-navigation_page_case{
  position: absolute;
  right: calc(32 / 1312 * 100%);
  bottom: calc(24 / 1312 * 100%);
  z-index: 5;
}

.swiper-button-prev, .swiper-button-next{
  width: 66px;
  height: 11px;
  opacity: 0.4;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  cursor: pointer;
}
.swiper-button-prev{  
  background-image: url('../images/arrow_left_white.svg');  
}

.swiper-button-next{
  background-image: url('../images/arrow_right_white.svg');
}
.swiper-pagination{
  display: inline-flex;
  color: white;
  font-size: 24px;
  font-family: $font-light;
  opacity: 0.7;
  margin-right: 24px;
  margin-left: 24px;
  align-items: center;
}
.swiper-pagination-current,
.swiper-pagination-total{
  font-size: 16px;
}
.swiper-pagination-current{
  margin-right: 8px;
}
.swiper-pagination-total{
  margin-left: 8px;
}

.swiper-slide_button{
  position: absolute;
  display: block;
  bottom: calc(126 / 712 * 100%);
  left: -24px;
  z-index: 5;
}