.article{
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  cursor: pointer;
  &__line{
    width: 100%;
    height: 1px;
    background-color: white;
    opacity: 0;
    margin-top: auto;
    transition: 0.2s;
  }
  &__inner{
    position: absolute;
    top:0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 40px;
    color: white;
    display: flex;
    flex-direction: column;
    &_padding_lg{
      padding: 48px;
    }
  }
  &__image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    &:after{
      content: '';
      display:block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    }
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc{
    font-family: $font-light;
    @include lineAndSpacing(160%);
    opacity: 0;    
    transition: 0.2s;
  }
  &__label{
    //margin-top: auto;
    padding-top: 32px;
    //border-top: 1px solid rgba(255,255,255, 0);
    transition: 0.2s;
  }
  &__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;
  }
  &__title{
    font-family: $font-regular;
    font-size: 24px;
    @include lineAndSpacing(120%);
  }
  &__arrow{
    width: 66px;
    height: 11px;
    background-position: right center;
    background-repeat: no-repeat;
    background-image: url('../images/arrow_right_white.svg');    
    transition: 0.2s;
    opacity: 0;
  }
  &:hover{
    .article{
      &__desc{
        opacity: 0.6;
      }
      &__line{
        opacity: 0.2;
      }
      // &__label{
      //   border-top: 1px solid rgba(255,255,255,.2);
      // }
      &__arrow{
        opacity: 1;
      }
    }
  }
  &_small{
    .article{
      
      &__inner{
        padding: 16px;
      }
      &__title{
        font-size: 14px;
        opacity: 0;
      }
      &__footer{
        margin-top: auto;
      }
    }
    &:hover{
      .article{
        &__title{
          opacity: 1;
        }
        &__footer{
          border-top: 1px solid rgba(255,255,255,.2);
        }
      }      
    }
  }  
  &_2{
    .article{
      &__label{
        margin-top: auto;
      }
      &__footer{
        padding-top: 0;
      }
      &__line{
        margin-top: 16px;
        margin-bottom: 16px;
      }
    }   
  }
  
}