.medias{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: -80px;
  &_jc_start{
    justify-content: flex-start;
    .media{
      margin-right: 32px;
      &:nth-child(3n){
        margin-right: 0;
      }
    }
  }
}
.media{
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: calc(416 / 1312 * 100%);
  //width: calc(416 / 1312 * 100%);
  color: white;
  position: relative;
  margin-bottom: 80px;
  &:hover{
    .media__button{
      opacity: 1;
    }
  }
  &__image{
    position: relative;
    width: 100%;
    margin-bottom: 16px;
    &:after{
      content: '';
      display: block;
      width: 100%;      
      padding-top: calc(312 / 416 * 100%);
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
      position:relative;
    }
    & img{      
      @include positionAbsoluteImage();
    }
  }
  &__date{
    font-family: $font-light;
    font-size: 12px;
    @include lineAndSpacing(100%);
    text-transform: uppercase;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(255,255,255,.2);
    margin-bottom: 16px;
  }
  
  &__title{
    font-family: $font-regular;
    font-weight: normal;
    font-size: 24px;
    @include lineAndSpacing(120%);
    //color: $font-color-dark;
  }
  &__button{
    position: absolute;
    left: -5%;
    top: -5%;
    opacity: 0;
    transition: 0.2s;
    cursor: pointer;
  }
  &_white{
    .media__date{
      border-bottom: 1px solid rgba(0,0,0,.2);
    }
    .media__title{
      color: $font-color-dark;
    }
  }
}
