.search-page{
  &__search{
    text-transform: uppercase;
  }
  &__first-screen{
    padding-top: 220px;
  }
  &__info{
    margin-bottom: 90px;
  }
  &__description{
    opacity: 0.6;
  }
  &__tabs{
    margin-bottom: 88px;
  }
  &__results-container{
    margin-bottom: 156px;
  }
  &__results[data-results-name="projects"]{
    margin-bottom: 104px;
  }
  &__results-title{
    font-size: 48px;
    @include lineAndSpacing(110%);
    color: $font-color-dark;
    position: relative;
    display: inline-block;
    margin-bottom: 48px;
  }
  &__results-index{
    position: absolute;
    display: block;
    left: 100%;
    top: 5px;
    opacity: 0.6;
  }

}
