.section_bg_dark{  
  background-color: #333333;
}
.section__card{
  max-width: 636px;
  margin-left: calc(680 / 1440 *100%);
}

//  Section main

.section_main{
  width: 100vw;
  height: 100vh;
  position: relative;
  padding: 32px 0 40px;
}
.section__scroll-line_main{
  position: absolute;  
  bottom: 0;    
  right: calc(64 / 1440 * 100%);
  z-index: 5;
}

.swiper-slider__container{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.swiper-container_main{
  width: 100%;
  height: 100%;
}
.swiper-slide{
  background-position: center 20%;
  background-size: cover;
  overflow: hidden;    
  &__inner{
    background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
    background-position: center;
    position: relative;
    width: 100vw;
    height: 100vh;   
    &:hover{      
      cursor: url(../images/cursor.png), auto;
    }
   
  }
  // &__image{
  //   width: 100%;
  //   height: 100%;
  //   img{
  //     width: 100%;
  //     left: 0;
  //     top: 50%;
  //     transform: translateY(-20%);
  //     display: block;
  //     z-index: -1;
  //   }
  //   &:after{
  //     content: '';
  //     position: absolute;
  //     display: block;
  //     left: 0;
  //     top: 0;
  //     right: 0;
  //     bottom: 0;
  //     background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  //   }
  // }
  &__title{
    font-family: $font-medium;
    font-size: 72px;
    font-weight: 500;
    @include lineAndSpacing(100%);
    color: white;
    display: block;
    position: absolute;
    left: 4.72%;
    top: 28.63%;
    max-width: 932px;
    pointer-events: none;
  }
  &__info{
    position: absolute;
    top: 56.58vh;    
    left: 41.81%;
    color: white;
    display: flex;
    flex-direction: row;
    pointer-events: none;
  }
  &__label{
    min-width: 46px;
    margin-right: 32px;
    pointer-events: none;
  }
  &__text{
    font-family: $font-light;
    font-size: 14px;
    @include lineAndSpacing(160%);
    max-width: 248px;
    pointer-events: none;
  }
  &__button{
    position: absolute;
    top: calc(324 / 744 * 100%);
    left: calc(1082 / 1440 * 100%);
  }
}
.wrapper_section_main{
  height: 100%;
  position: relative;
  //pointer-events: none;
}
.socials_section_main{
  margin-top: auto;
  position: absolute;
  bottom: 32px;
  left: 0;
  z-index: 5;
}
//  Section about



//  Section services

.services__container{
  width: 100%;  
  display: flex;
  justify-content: space-between;
  margin-bottom: -130px;
  position: relative;
  z-index: 1;
}
.service{
   width: calc(416 / 1312 * 100%);   
   position: relative;
   &:after{
    content: '';
    display: block;
    width: 100%;
    padding-top: 133.4%;
  }
}

//  Section community

.main__section_media{  
  padding-bottom: 72px;
}
.section_media{
  padding-top: 156px;
  position: relative;
  z-index: 2;
}
.section__media_wrapper{
  margin-top: 104px;
}

