// Section map
.section_map{
  position: relative;
  width: 100%;
  &:after{
    content: '';
    display: block;
    width: 100%;
    padding-top: 1168px;
    pointer-events: none;
  }
}
.map{
  bottom: 0;
  left: 0;
  position: absolute;
  width: 100%;
  z-index:1;  
}
.map__info{
  position: absolute;
  top: calc(156 / 1168 * 100%);
  left: calc(684 / 1440 * 100%);
  z-index: 2;
}
