.socials{
  display: flex;
  justify-content: flex-start;
  list-style: none;
  &__item{
    margin-left: 56px;
    &:first-child{
      margin-left: 0;
    }
  }
  &__link{
    font-family: 'Cera PRO Light', sans-serif;
    color: white;
    font-size: 12px;
    letter-spacing: -0.02em;
  }

  &_space-between{
    width: 100%;
    justify-content: space-between;
    .socials__item{
      margin:  0;
    }
  }

  &_dark{
    .socials{
      &__link{
        color: $font-color-dark;
      }
    }
  }
}
